import ApiClient from "../../infrastructure/api/apiClient";
import {API_ENDPOINTS} from "../../constants/apiEndpoints";
import {extractErrorMessage} from "../../utils/errorHandler";
import type {CalculatedTotals} from "../../types/checkout";

export interface DatafastCheckoutRequest {
	// ‚úÖ SINCRONIZADO CON PHP: Refleja exactamente las validaciones del backend
	shippingAddress: {
		street: string;          // required|string|max:100
		city: string;            // required|string|max:50
		country: string;         // required|string|max:100
		// ‚úÖ NO incluir identification aqu√≠ - usar solo customer.doc_id
	};
	customer: {                  // required|array - OBLIGATORIO PARA SRI
		given_name?: string;     // sometimes|string|max:48
		middle_name?: string;    // sometimes|string|max:50
		surname?: string;        // sometimes|string|max:48
		phone?: string;          // sometimes|string|min:7|max:25
		doc_id: string;          // required|string|size:10 - OBLIGATORIO PARA SRI
	};
	items?: {                    // sometimes|array
		product_id: number;
		quantity: number;
		price: number;
	}[];
	total: number;               // required|numeric|min:0.01
	subtotal?: number;           // sometimes|numeric|min:0
	shipping_cost?: number;      // sometimes|numeric|min:0
	tax?: number;                // sometimes|numeric|min:0
	discount_code?: string | null; // sometimes|string|nullable
	discount_info?: any;         // sometimes|array|nullable
	// ‚úÖ CAMPOS PARA CHECKOUTDATA TEMPORAL
	session_id?: string;         // sometimes|string|max:100
	validated_at?: string;       // sometimes|string
}

// ‚úÖ ESTANDARIZADO: Response unificada con backend
export interface DatafastCheckoutResponse {
	// ‚úÖ DECISI√ìN: Usar 'success' como campo principal booleano
	success: boolean;               // Campo principal de estado
	status: 'success' | 'failed' | 'error'; // ‚úÖ OBLIGATORIO: Estado descriptivo
	data?: {
		checkout_id: string;        // ID del checkout de Datafast
		widget_url: string;         // URL del widget para el pago
		transaction_id: string;     // ID √∫nico del sistema (ORDER_xxx)
		amount: number;             // Monto total a pagar
	};
	message: string;                // ‚úÖ OBLIGATORIO: Mensaje descriptivo del resultado
	error_code?: string;            // C√≥digo de error espec√≠fico si falla
}

export interface DatafastVerifyPaymentRequest {
	resource_path: string;
	transaction_id: string;
	calculated_total?: number; // ‚úÖ OPCIONAL: Para verificaci√≥n adicional de seguridad - Backend valida contra datos almacenados
	session_id?: string; // ‚úÖ OPCIONAL: Para arquitectura centralizada - Permite recuperar CheckoutData temporal
}

// ‚úÖ ESTANDARIZADO: Response de verificaci√≥n unificada
export interface DatafastVerifyPaymentResponse {
	// ‚úÖ DECISI√ìN: Usar 'success' como campo principal booleano
	success: boolean;
	status: 'success' | 'processing' | 'error' | 'pending'; // ‚úÖ OBLIGATORIO: Estado descriptivo
	data?: {
		order_id: number;             // ‚úÖ CORREGIDO: ID num√©rico de la orden
		order_number: string;         // N√∫mero de orden para mostrar al usuario
		total: number;                // Total de la orden
		payment_status: 'completed' | 'pending' | 'failed' | 'error'; // ‚úÖ TIPADO FUERTE
		payment_id: string;           // ID del pago de Datafast
		transaction_id: string;       // ID √∫nico del sistema
		processed_at: string;         // Timestamp ISO 8601
	};
	message: string;                  // ‚úÖ OBLIGATORIO: Mensaje descriptivo del resultado
	result_code?: string;             // C√≥digo de resultado de Datafast
	is_phase_1_error?: boolean;       // Indicador de error t√≠pico de Fase 1
}

// ‚úÖ INTERFACES TIPADAS FUERTEMENTE - SINCRONIZADAS CON PHP
export interface ShippingData {
	street: string;        // required|string|max:100
	city: string;          // required|string|max:50
	country: string;       // required|string|max:100
	identification?: string; // sometimes|string|max:13
}

export interface BillingData {
	street: string;
	city: string;
	country: string;
	identification?: string;
}

export interface CartItem {
	product_id: number;    // required|integer
	quantity: number;      // required|integer|min:1
	price: number;         // required|numeric|min:0
	name?: string;         // Para mostrar en UI
	subtotal?: number;     // Calculado: price * quantity
}

export interface OrderTotals {
	subtotal: number;      // required|numeric|min:0
	shipping_cost: number; // required|numeric|min:0
	tax: number;           // required|numeric|min:0
	discount: number;      // required|numeric|min:0
	final_total: number;   // required|numeric|min:0.01
}

export interface DiscountInfo {
	type: 'volume' | 'coupon' | 'feedback';
	amount: number;
	percentage?: number;
	code?: string;
	description?: string;
}

// ‚úÖ INTERFACES PARA ARQUITECTURA CENTRALIZADA - FUERTEMENTE TIPADAS
export interface StoreCheckoutDataRequest {
	shippingData: ShippingData;      // ‚úÖ TIPADO FUERTE
	billingData: BillingData;        // ‚úÖ TIPADO FUERTE
	items: CartItem[];               // ‚úÖ TIPADO FUERTE - min:1 validado en PHP
	totals: CalculatedTotals;        // ‚úÖ TIPADO FUERTE - Cambio de OrderTotals a CalculatedTotals
	sessionId: string;               // required|string|max:100
	discountCode?: string | null;    // sometimes|string|nullable
	discountInfo?: {
		code: string;
		discount_percentage: number;
		discount_amount: number;
	};   // ‚úÖ TIPADO FUERTE - informaci√≥n de descuento (alineado con CheckoutData)
}

// ‚úÖ ESTANDARIZADO: Response de almacenamiento de checkout
export interface StoreCheckoutDataResponse {
	success: boolean;
	status: 'success' | 'failed' | 'error'; // ‚úÖ A√ëADIDO: Consistencia con otras responses
	message: string;
	data: {
		session_id: string;         // Identificador √∫nico de sesi√≥n
		expires_at: string;         // Timestamp ISO 8601 de expiraci√≥n
		final_total: number;        // Total calculado final
	};
	error_code?: string;            // ‚úÖ A√ëADIDO: C√≥digo de error si falla
}

export class DatafastService {
	/**
	 * Almacenar CheckoutData temporal para arquitectura centralizada
	 * ‚úÖ CORREGIDO: Usa interfaces fuertemente tipadas
	 */
	async storeCheckoutData(
		checkoutData: StoreCheckoutDataRequest
	): Promise<StoreCheckoutDataResponse> {
		try {
			console.log("DatafastService: Almacenando CheckoutData temporal", checkoutData);

			const response = await ApiClient.post<StoreCheckoutDataResponse>(
				API_ENDPOINTS.DATAFAST.STORE_CHECKOUT_DATA,
				checkoutData
			);

			console.log("DatafastService: CheckoutData almacenado exitosamente", response);

			return response;
		} catch (error) {
			console.error("DatafastService: Error al almacenar CheckoutData:", error);

			const errorMessage = extractErrorMessage(
				error,
				"Error al almacenar datos de checkout"
			);

			throw new Error(errorMessage);
		}
	}

	/**
	 * Crear un checkout de Datafast
	 */
	async createCheckout(
		checkoutData: DatafastCheckoutRequest
	): Promise<DatafastCheckoutResponse> {
		try {
			console.log("DatafastService: Creando checkout", checkoutData);

			const response = await ApiClient.post<DatafastCheckoutResponse>(
				API_ENDPOINTS.DATAFAST.CREATE_CHECKOUT,
				checkoutData
			);

			console.log("DatafastService: Respuesta de checkout", response);

			return response;
		} catch (error) {
			console.error("DatafastService: Error al crear checkout:", error);

			const errorMessage = extractErrorMessage(
				error,
				"Error al crear el checkout de Datafast"
			);

			throw new Error(errorMessage);
		}
	}

	/**
	 * Verificar el estado del pago
	 */
	async verifyPayment(
		verifyData: DatafastVerifyPaymentRequest
	): Promise<DatafastVerifyPaymentResponse> {
		try {
			console.log("DatafastService: Verificando pago", verifyData);

			const response = await ApiClient.post<DatafastVerifyPaymentResponse>(
				API_ENDPOINTS.DATAFAST.VERIFY_PAYMENT,
				verifyData
			);

			console.log("DatafastService: Respuesta de verificaci√≥n", response);

			return response;
		} catch (error) {
			console.error("DatafastService: Error al verificar pago:", error);

			const errorMessage = extractErrorMessage(
				error,
				"Error al verificar el pago de Datafast"
			);

			throw new Error(errorMessage);
		}
	}

	/**
	 * Simular una transacci√≥n exitosa para pruebas en Fase 1
	 * (En producci√≥n, esto no debe usarse)
	 */
	async simulateSuccessfulPayment(
		checkoutId: string,
		transactionId: string,
		calculatedTotal?: number
	): Promise<DatafastVerifyPaymentResponse> {
		if (!checkoutId) {
			throw new Error("checkout_id es requerido para simular el pago");
		}

		if (!transactionId) {
			throw new Error("transaction_id es requerido para simular el pago");
		}

		// Simular el resourcePath que normalmente viene del widget
		const mockResourcePath = `/v1/checkouts/${checkoutId}/payment`;

		console.log("DatafastService: Simulando pago exitoso", {
			checkoutId,
			transactionId,
			mockResourcePath,
		});

		try {
			// ‚úÖ CORREGIDO: Llamar al endpoint de verificaci√≥n con simulate_success en el body
			const requestData = {
				resource_path: mockResourcePath,
				transaction_id: transactionId,
				calculated_total: calculatedTotal, // ‚úÖ ENVIAR TOTAL CALCULADO
				simulate_success: true, // ‚úÖ MOVIDO DEL QUERY PARAMETER AL BODY
			};

			console.log("üîÑ Enviando datos de simulaci√≥n:", requestData);

			const response = await ApiClient.post<DatafastVerifyPaymentResponse>(
				API_ENDPOINTS.DATAFAST.VERIFY_PAYMENT, // ‚úÖ SIN QUERY PARAMETER
				requestData
			);

			console.log("DatafastService: Respuesta de simulaci√≥n:", response);

			return response;
		} catch (error) {
			console.error("DatafastService: Error en simulaci√≥n:", error);

			const errorMessage = extractErrorMessage(
				error,
				"Error al simular el pago de Datafast"
			);

			throw new Error(errorMessage);
		}
	}

	/**
	 * Manejar el resultado real de Datafast (cuando viene del widget)
	 */
	async handleDatafastResult(
		resourcePath: string,
		transactionId: string,
		calculatedTotal?: number
	): Promise<DatafastVerifyPaymentResponse> {
		try {
			console.log("DatafastService: Manejando resultado real de Datafast", {
				resourcePath,
				transactionId,
				calculatedTotal,
			});

			const response = await this.verifyPayment({
				resource_path: resourcePath,
				transaction_id: transactionId,
				calculated_total: calculatedTotal, // ‚úÖ INCLUIR TOTAL CALCULADO
			});

			// Si es el error t√≠pico de Fase 1, devolver un mensaje m√°s claro
			if (response.status !== "success" && response.result_code === "800.900.300") { // ‚úÖ CORREGIDO: Cambiar response.success por response.status
				return {
					success: false,
					status: 'error',
					message:
						'No se complet√≥ un pago real. Este es el comportamiento esperado en Fase 1 de pruebas. Use "Simular Pago Exitoso" para probar el flujo completo.',
					result_code: response.result_code,
					is_phase_1_error: true,
				};
			}

			return response;
		} catch (error) {
			console.error("DatafastService: Error al manejar resultado:", error);

			const errorMessage = extractErrorMessage(
				error,
				"Error al procesar el resultado de Datafast"
			);

			throw new Error(errorMessage);
		}
	}

	/**
	 * Simular el flujo completo del widget (id√©ntico al comportamiento real)
	 * Esta funci√≥n replica exactamente lo que har√≠a el widget real sin consumir cr√©dito
	 * IMPORTANTE: Usa el endpoint de verificaci√≥n con simulate_success=true para garantizar 
	 * que pase por EXACTAMENTE el mismo proceso: crear orden + factura + env√≠o SRI
	 */
	async simulateCompleteWidgetFlow(
		checkoutId: string,
		transactionId: string,
		calculatedTotal: number,
		formData: any
	): Promise<string> {
		try {
			console.log("üéØ DatafastService: Simulando flujo COMPLETO del widget (orden + factura + SRI)");
			console.log("üìä Par√°metros:", { checkoutId, transactionId, calculatedTotal });
			
			// 1. Simular el resourcePath que generar√≠a el widget real
			const mockResourcePath = `/v1/checkouts/${checkoutId}/payment`;
			console.log("üîó ResourcePath simulado:", mockResourcePath);
			
			// 2. Guardar datos en localStorage EXACTAMENTE igual que el widget real
			console.log("üíæ Guardando datos en localStorage (id√©ntico a widget real)...");
			
			// ResourcePath (usado por DatafastResultPage)
			localStorage.setItem("datafast_resource_path", mockResourcePath);
			
			// Form data (usado por DatafastResultPage para crear orden)
			localStorage.setItem("datafast_form_data", JSON.stringify(formData));
			
			// Total calculado (para verificaci√≥n en resultado)
			localStorage.setItem("datafast_calculated_total", calculatedTotal.toString());
			
			// Transaction y checkout IDs (ya deber√≠an estar guardados pero asegurar)
			localStorage.setItem("datafast_transaction_id", transactionId);
			localStorage.setItem("datafast_checkout_id", checkoutId);
			
			console.log("‚úÖ Datos guardados en localStorage:");
			console.log("   - datafast_resource_path:", mockResourcePath);
			console.log("   - datafast_form_data:", "guardado");
			console.log("   - datafast_calculated_total:", calculatedTotal);
			console.log("   - datafast_transaction_id:", transactionId);
			console.log("   - datafast_checkout_id:", checkoutId);
			
			// 3. ‚úÖ CR√çTICO: NO hacer request prematuramente
			// El √∫nico request debe ser cuando DatafastResultPage procese la URL
			// Esto evita el problema de doble request que causaba "carrito vac√≠o"
			console.log("üìã Configuraci√≥n lista - DatafastResultPage procesar√° el pago");
			console.log("‚ö†Ô∏è NO hacer request aqu√≠ para evitar doble procesamiento");
			
			// 4. Simular delay del widget (m√°s realista)
			console.log("‚è≥ Simulando delay de procesamiento del widget...");
			await new Promise(resolve => setTimeout(resolve, 1000));
			
			// 5. Construir URL de redirecci√≥n id√©ntica al widget real
			// DatafastResultPage procesar√° esto y seguir√° el flujo completo:
			// verifyPayment() ‚Üí processCheckout() ‚Üí orden + factura + SRI
			// ‚úÖ AGREGADO: simulate=true para activar DatafastTestValidator
			const resultUrl = `/datafast-result?resourcePath=${encodeURIComponent(mockResourcePath)}&status=pending&transactionId=${transactionId}&simulate=true`;
			console.log("üöÄ URL de redirecci√≥n generada (flujo completo):", resultUrl);
			console.log("üìã DatafastResultPage ejecutar√°:");
			console.log("   1. verifyPayment() - verificaci√≥n (simulada como exitosa)");  
			console.log("   2. processCheckout() - crear orden + factura + enviar SRI");
			console.log("   3. clearCart() - limpiar carrito");
			console.log("   4. navigate('/orders') - redirigir a √≥rdenes");
			
			return resultUrl;
			
		} catch (error) {
			console.error("‚ùå Error en simulaci√≥n de widget flow:", error);
			throw new Error("Error al simular el flujo del widget");
		}
	}

	/**
	 * Extraer resourcePath de la URL de respuesta
	 */
	extractResourcePath(url: string): string | null {
		try {
			const urlParams = new URLSearchParams(url.split("?")[1]);
			const resourcePath = urlParams.get("resourcePath");
			console.log("DatafastService: ResourcePath extra√≠do:", resourcePath);
			return resourcePath;
		} catch (error) {
			console.error("DatafastService: Error al extraer resourcePath:", error);
			return null;
		}
	}
}
